<template>
    <div id="mpprogress">
        <div 
        ref="progressbar"
        id="totaltime"
        @click="clickprogress($event)"
        >
            <div 
            id="time"
            ref="time"
            >
            </div>
        </div>
    </div>
</template>
<script>
    import {mapState,mapGetters,mapMutations} from 'vuex'
    export default {
        data(){
            return {

            }
        },
        computed:{
            ...mapState('player',['currentTime']),
            ...mapGetters('player',['getduration','getcurrentTime'])
        },
        methods:{
            ...mapMutations("player",["SET_PROGRESSCURRENTTIME"]),
            clickprogress(e){
                var progress = (e.clientX - this.$refs.progressbar.getBoundingClientRect().left) / this.$refs.progressbar.clientWidth
			    this.SET_PROGRESSCURRENTTIME(this.getduration*progress)
                console.log(this.getduration*progress)
			    
            }
        },
        watch:{
            currentTime(newVal,oldVal){
                var pe = (this.getcurrentTime / this.getduration)*100
                this.$refs.time.style.width=`${pe}%`
            }
        },
        mounted() {
            var pe = (this.getcurrentTime / this.getduration)*100
            this.$refs.time.style.width=`${pe}%`
        }
    }    
</script>
<style lang="scss">
    #mpprogress{
        flex:1;
        #totaltime{
            height: 100%;
            background-color: #ccc;
            #time{
                height:100%;
                width:0%;
                background-color: #fff;
            }
        }
    }  
</style>



