<template>
    <div>
        <div id="audiobox">
            <audio
                id="audio" 
                ref="audio"
                :src="currentItem.file"
                @loadedmetadata="canplay"
                @timeupdate="timeupdata"
                @ended="musicEnded"
            ></audio>
            <div id="audioimgbox">
                <img id="audioimg" :src="currentItem.cover" alt="audioimg">
            </div>
            <div id="audionav">
                <div id="navctx">
                    <div id="navtitle">
                        {{currentItem.title}}
                        <br>
                        {{currentItem.artist}}
                    </div>
                    <div id="navlrc">
                        {{navlrc}}
                    </div>
                </div>
                <div id="navplay">
                    <img
                    ref="navstartBtn"
                    class="tool-png start" 
                    :src="startBtnURL" 
                    alt="start"
                    @click="mpStart"
                    >
                </div>
            </div>
        </div>
        
    </div>
</template>
<script>
    import {mapState,mapGetters,mapMutations} from 'vuex'
    import startPng from "../images/start.png"
    import pausedPng from "../images/paused.png"
    export default {
        data(){
            return {
                navlrc:"",
                navlrcindex:0,
                startBtnURL:startPng
            }
        },
        computed:{
            ...mapState('player',['paused','currentTime','lrcindex','progresscurrentTime']),
            ...mapGetters('list',["currentItem",'getLrcObjList','getMaxIndex','getLrc','getrepeatType','getcurrentIndex']),
            ...mapGetters('player',['mpPaused','getduration','getlrcindex','getcurrentTime','getprogresscurrentTime'])
        },
        watch:{
            paused(newVal,oldVal){
                var audio = this.$refs.audio
                if(newVal==true){
                    audio.pause()
                }
                else if(newVal==false){
                    audio.play()
                }
            },
            lrcindex(newVal,oldVal){
                this.navlrc = this.getLrcObjList[this.getlrcindex].LRC
            },
            progresscurrentTime(){
                this.$refs.audio.currentTime = this.getprogresscurrentTime
            },
            currentTime(newVal,oldVal){
                var cutTime = Math.floor(this.getcurrentTime)
                var LrcObjList = this.getLrcObjList
                var loll = LrcObjList.length
                var nowlrcindex = 0;
                for(var i=0;i<loll;i++){
                    if(LrcObjList[i].time == cutTime){
                        nowlrcindex = i
                        if(this.navlrcindex!=nowlrcindex){
                            this.navlrcindex = i
                            this.SET_LRCINDEX(i)
                        }
                    }
                }
            },
            mpPaused(newVal,oldVal){
                this.changeUrl()
            } 
        },
        methods:{
            ...mapMutations('list',['SET_REPEATTYPE','CHANGE_INDEX','PLAY_RETURN']),
            ...mapMutations('player',['PAUSED','SET_PAUSED','SET_DURATION','SET_CURRENTTIME','SET_LRCINDEX']),
            canplay(){
                this.SET_DURATION(this.$refs.audio.duration)
            },
            timeupdata(){
                this.navlrc = this.getLrcObjList[this.getlrcindex].LRC
                this.SET_CURRENTTIME(this.$refs.audio.currentTime)
            },
            mpStart(){
                console.log("btn")
                this.SET_PAUSED()
            },
            changeUrl(){
                if(this.mpPaused==true){
                    this.startBtnURL=startPng
                }
                else if(this.mpPaused==false){
                    this.startBtnURL=pausedPng
                }
            },
            musicEnded(){
                console.log("music is end")
                if(this.getrepeatType=='cycle'){
                    // console.log("must repeat")
                    this.$refs.audio.play()
                }
                else if(this.getrepeatType=='list'){
                    if(this.getcurrentIndex>=this.getMaxIndex-1){
                        this.PAUSED()
                        this.PLAY_RETURN()
                        setTimeout(()=>{
                            console.log("start")
                            this.SET_PAUSED()
                        },500)
                    }
                    else{
                        this.CHANGE_INDEX(1)
                        this.PAUSED()
                        setTimeout(()=>{
                            console.log("start")
                            this.SET_PAUSED()
                        },500)
                    }
                }
            }
        },
        mounted(){
           
        }
    }
</script>
<style lang="scss">
    #audiobox{
        height: 0.7rem;
        background-color: orangered;
        display: flex;
        flex-direction: row;

    }
    
    #audioimgbox{
        flex:2;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        #audioimg{
            height:0.5rem;
            width:0.5rem;
            border:1px solid white;
        }
    }
    #audionav{
        flex:8;
        display: flex;
        flex-direction: row;
        #navctx{
            flex:8;
            display: flex;
            flex-direction: column;
            #navtitle{
                flex:6;
                display: flex;
                align-items: center;
                font-size: 0.1rem;
                color:white;
            }
            #navlrc{
                flex:4;
                display: flex;
                align-items: center;
                font-size:0.1rem;
                color: #ccc;
            }
        }
        #navplay{
            flex:2;
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }
    .tool-png{
        height: 0.5rem;
        width: 0.5rem;
    }
    .start{
        height: 0.4rem;
        width: 0.4rem;
        margin-left: 10px;
    }
</style>



