<template>
    <div 
        id="musicplayer"
        v-swipeleft="swipeleft" 
        v-swiperight="swiperight"
    >
       <div id="imgbox">
           <img
           id="mpimg"
           :class="imgclass"
           :src="currentItem.cover" 
           alt="musicimg">
       </div>
       <div id="tools">
           <mpprogress></mpprogress>
           <tool id="tool"></tool>
       </div>
    </div>
</template>
<script>
    import {mapState,mapGetters,mapMutations} from 'vuex'
    import mpprogress from "./mpprogress"
    import tool from "./tool"

    export default {
        name:"player",
        data(){
            return {
                imgclass:""
            }
        },
        components:{
            mpprogress,
            tool
        },
        computed:{
            ...mapState('player',['paused']),
            ...mapGetters('player',['mpPaused','getlrcindex']),
            ...mapGetters('list',["currentItem"])  
        },
        methods:{
            swiperight(s,e){
                this.$router.push("list",()=>{
                    console.log("list")
                })
            },
            swipeleft(s,e){
                this.$router.push("lrc",()=>{
                    console.log("lrc")
                })
            }
        },
        watch:{
            mpPaused(newVal,oldVal){
                if(newVal==true){
                    this.imgclass=""
                }
                else if(newVal==false){
                    this.imgclass="Rotation"
                }
            }
        },
        mounted(){
            if(this.mpPaused==true){
                this.imgclass=""
            }
            else if(this.mpPaused==false){
                this.imgclass="Rotation"
            }
        }
    }
</script>
<style lang="scss">
    @keyframes rotation {
        from {-webkit-transform: rotate(0deg);
    }to {
        -webkit-transform: rotate(360deg);
    }}
    .Rotation {
        transform: rotate(360deg);
        animation: rotation 8s linear infinite;
    }
    #musicplayer{
        width: 100%;
        height: 100%;
        position: absolute;
        background-color: #fff;
        font-size:0.1rem;
        display: flex;
        flex-direction: column;
        #imgbox{
            flex:7;
            overflow: hidden;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: rgba(0,0,0,0.4);
            #mpimg{
                height:2.5rem;
                width:2.5rem;
                border-radius: 50%;
                align-items: center;
            }
        }
        #tools{
            flex:3;
            background-color: rgba(0,0,0,0.5);
            display: flex;
            flex-direction: column;
            #tool{
                flex:9;
            }
        }
    }
</style>



