export default {
    mpPaused(state){
        return state.paused
    },
    getduration(state){
        return state.duration
    },
    getcurrentTime(state){
        return state.currentTime
    },
    getlrcindex(state){
        return state.lrcindex
    },
    getprogresscurrentTime(state){
        return state.progresscurrentTime
    }
}


