<template>
    <div>
        <div id="toolbox">
            <ul id="toolul">
                <li>
                   <img
                    class="tool-png" 
                    :src="cyclebtnURL"
                    alt="cyclebtn"
                    @click="changeRepeat"
                    >
                </li>
                <li>
                    <img 
                    class="tool-png"
                    src="../images/last.png" 
                    alt="last"
                    @click="mplast"
                    >
                </li>
                <li>
                    <img
                    ref="startBtn"
                    class="tool-png start" 
                    :src="startBtnURL" 
                    alt="start"
                    @click="mpStart"
                    >
                </li>
                <li>
                    <img 
                    class="tool-png"
                    src="../images/next.png"
                     alt="next"
                     @click="mpnext"
                     >
                </li>
                <li>
                    <img class="tool-png" 
                    src="../images/volume-up.png" 
                    alt="volume-up"

                    >
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
    import {mapState,mapGetters,mapMutations} from 'vuex'
    import startPng from "../images/start.png"
    import pausedPng from "../images/paused.png"
    import cyclePng from "../images/cycle.png"
    import listplayPng from "../images/listplay.png"

    export default {
        data(){
            return {
                startBtnURL:startPng,
                cyclebtnURL:cyclePng
            }
        },
        computed:{
            ...mapGetters('player',['mpPaused']),
            ...mapGetters('list',["currentItem","getMaxIndex","getrepeatType"]),
            
        },
        methods:{
            ...mapMutations('player',['SET_PAUSED','PAUSED']),
            ...mapMutations('list',['CHANGE_INDEX','PLAY_LAST','PLAY_RETURN',"SET_REPEATTYPE"]),
            mpStart(){
                this.SET_PAUSED()
            },
            changeRepeat(){
                if(this.getrepeatType=="cycle"){
                    this.SET_REPEATTYPE("list")
                    this.cyclebtnURL=listplayPng                   

                }
                else if(this.getrepeatType=="list"){
                    this.SET_REPEATTYPE("cycle")    
                    this.cyclebtnURL = cyclePng               
                }
            },
            mplast(){
                if(this.currentItem.id<=1){
                    this.PLAY_LAST()
                }
                else{
                    this.CHANGE_INDEX(-1)
                }
                this.PAUSED()
                setTimeout(()=>{
                            console.log("start")
                            this.SET_PAUSED()
                        },500)
            },
            mpnext(){
                if(this.currentItem.id>=this.getMaxIndex){
                    this.PLAY_RETURN()
                }
                else{
                    this.CHANGE_INDEX(1)
                }
                this.PAUSED()
                setTimeout(()=>{
                            console.log("start")
                            this.SET_PAUSED()
                        },500)
            },
            changeUrl(){
                if(this.mpPaused==true){
                    this.startBtnURL=startPng
                }
                else if(this.mpPaused==false){
                    this.startBtnURL=pausedPng
                }
            },
            changerepeatURL(){
                var thisrepeatType = this.getrepeatType
                if(thisrepeatType=="cycle"){
                    this.cyclebtnURL = cyclePng
                }
                else if(thisrepeatType=="list"){
                    this.cyclebtnURL = listplayPng
                }
            }
        },
        watch:{
            mpPaused(newVal,oldVal){
                this.changeUrl()
            }
        },
        mounted(){
            this.changeUrl()
            this.changerepeatURL()
        }
    }    
</script>]
<style lang="scss">
    #toolbox{
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    #toolul{
        display: flex;
        flex-direction: row;
        height: 0.5rem;
        // background-color: red;
        li{
            list-style: none;
            flex:2;
            display: flex;
            justify-content: center;
            align-items: center;
            // border: 1px solid black;
        }
        .tool-png{
            height: 0.5rem;
            width: 0.5rem;
        }
        .start{
            height: 0.4rem;
            width: 0.4rem;
            margin-left: 10px;
        }
    }
</style>



