import * as Types from './mutations-types'
export default {
    [Types.CHANGE_INDEX](state,num){
        state.currentIndex+=num
    },
    [Types.PLAY_LAST](state){
        let lastindex = state.musicList.length
        state.currentIndex = lastindex-1
    },
    [Types.PLAY_RETURN](state){
        state.currentIndex = 0
    },
    [Types.SET_REPEATTYPE](state,type){
        state.repeatType = type
    },
    [Types.SET_INDEX](state,num){
        state.currentIndex = num
    }
}


