import {lrcHandle2} from '../../assets/lrcHandle2.js'
export default {
    currentItem(state){
        return state.musicList[state.currentIndex]
    },
    getMusicList(state){
        return state.musicList
    },
    getcurrentIndex(state){
        return state.currentIndex
    },
    getMaxIndex(state){
        return state.musicList.length
    },
    getLrc(state){
        return state.musicList[state.currentIndex].lrc
    },
    getLrcObjList(state){
        return lrcHandle2(state)
    },
    getItemImgUrl(state){
        return state.musicList[state.currentIndex].cover
    },
    getrepeatType(state){
        return state.repeatType
    }
}


