import * as Types from './mutations-types'
export default {
    [Types.SET_PAUSED](state){
        state.paused=!state.paused
    },
    [Types.PAUSED](state){
        state.paused = true
    },
    [Types.SET_DURATION](state,num){
        state.duration = num
    },
    [Types.SET_CURRENTTIME](state,num){
        state.currentTime = num
    },
    [Types.SET_LRCINDEX](state,num){
        state.lrcindex = num
    },
    [Types.SET_PROGRESSCURRENTTIME](state,num){
        state.progresscurrentTime=num
    }
    
}


