<template>
    <div id="main">
        <transition :name="transitionName" >
            <router-view class="childview" mode="out-in"></router-view>
        </transition>
    </div>
    
</template>
<script>
    export default {
        name: 'App',
        data(){
            return {
                transitionName:""
            }
        },
        watch: {
            "$route"(to, from) {
                if (to.meta.index > from.meta.index) {
                    this.transitionName = 'slide-right';
                }
                else {
                    this.transitionName = 'slide-left';
                }
            }
        }
    }
</script>
<style lang="scss">
    #main{
        flex:8;
        position: relative;
        overflow:hidden;
    }
    .slide-right-enter-active,.slide-right-leave-active{
        transition: all 0.5s;
    }
    .slide-right-enter{
        transform: translate3d(100%,0,0);
        // opacity: 0;
    }
    .slide-right-leave-to{
        transform: translate3d(-100%,0,0);
        // opacity: 0;
    }
    .slide-left-enter-active,.slide-left-leave-active{
        transition: all 0.5s;
    }
    .slide-left-enter{
        // opacity: 0;
        transform: translate3d(-100%, 0, 0)
    }
    .slide-left-leave-to{
        // opacity: 0;
        transform: translate3d(100%, 0, 0)
    }
</style>




