<template>
  <div id="app">
      <router-view name="header"></router-view>
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
      <router-view name="footer"></router-view>
  </div>
</template>

<script>
import flexible from './assets/flexible'
flexible()
export default {
  name: 'App'
}
</script>

<style>
  *{
    margin:0;
    padding:0;
  }
  #app{
    overflow: hidden;
    height:100vh;
    display: flex;
    flex-direction: column;
    background-color: #ccc;
  }
</style>



