export const lrcHandle2=(state)=>{
    var mplrc = state.musicList[state.currentIndex].lrc
    var re = /\[(.+?)\n/g
    var re2 = /\[([^)]*)\]/
    var re3 = /\]/
    var list = mplrc.match(re)
    var objlist=[]
    var musictime=""
    var minutes=0,seconds=0,millisecond=0,m=0,s=0,ms=0
    for(var i=0;i<list.length;i++){
        var obj={}
        musictime = list[i].match(re2)[1]
        var m =Number(musictime.slice(0,2))
        var s = Number(musictime.slice(3,5))
        var ms = Number(musictime.slice(6))
        minutes = m*60
        seconds = s
        millisecond = ms/1000
        obj.time = Math.floor(minutes+seconds+millisecond)
        obj.LRC = list[i].slice((list[i].search(re3)+1))
        objlist.push(obj)
    }
    return objlist
    
}



