<template>
    <div 
        id="lrc"
        v-swiperight="swiperight"
    >
        <div id="contextbox" ref="ctxbox">
            <div id="context">
                <ul id="lrcUl" ref="lrcul">
                    <li
                    ref="lrcli" 
                    v-for="(lrc,index) in getLrcObjList" 
                    :key="index"
                    :class="index==lrcindex?'green':'gray'"
                    >{{lrc.LRC}}</li>
                </ul>
            </div>
        </div>
        
    </div>
</template>
<script>
    import {mapState,mapGetters,mapMutations} from "vuex"
    export default {
        name:"lrc",
        data(){
            return {
                lrcindex:0
            }
        },
        computed:{
            ...mapState('player',['duration','currentTime']),
            ...mapGetters('list',["getLrc","getLrcObjList"]),
            ...mapGetters('player',['getduration','getcurrentTime','getlrcindex'])
        },
        methods:{
            ...mapMutations('player',['SET_LRCINDEX']),
            swiperight(s,e){
                this.$router.push("/",()=>{
                    console.log("player")
                })
            }
        },
        watch:{
            currentTime(newVal,oldVal){
                var cutTime = Math.floor(this.getcurrentTime)
                var LrcObjList = this.getLrcObjList
                var loll = LrcObjList.length
                var nowlrcindex = 0;
                for(var i=0;i<loll;i++){
                    if(LrcObjList[i].time == cutTime){
                        nowlrcindex = i
                        if(this.lrcindex!=nowlrcindex){
                            this.lrcindex = i
                            this.SET_LRCINDEX(i)
                            this.$refs.lrcul.style.marginTop=`${this.lrcindex*-0.4}rem`
                        }
                    }
                }
            } 
        },
        mounted(){
            var mt = this.getlrcindex
            this.lrcindex = this.getlrcindex
            this.$refs.lrcul.style.marginTop=`${mt*-0.4}rem`
        }
    }
</script>]
<style lang="scss">
    li{
        list-style: none;
    }
    // li:first-child{
    //     background-color: red;
    // }
    
    #lrc{
        width: 100%;
        height: 100%;
        position: absolute;
        background-color: rgba(0,0,0,0.5);
        font-size:0.1rem;
        display: flex;
        justify-content: center;
        flex-direction: column;
        #contextbox{
            height: 5rem;
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            overflow: hidden;
            justify-content: center;
            #context{
                height: 2rem;
                width: 100%;
                display: flex;
                flex-direction: column;
                align-items: center;
                #lrcUl{
                    width: 100%;
                    margin-top:0rem;  //初始歌词位置
                    li{
                        height:0.3rem;
                        margin-top:0.1rem;
                        font-size:0.18rem;
                        text-align: center;
                    }
                }
            }
        }
    }
    .green{
        color:#fff;
    }
    .gray{
        font-weight: 700;
        color:rgba(0,0,0,0.3)
    }
</style>




