<template>
    <div 
        id="list"
        v-swipeleft="swipeleft"
        v-swiperight="swiperight"
    >
        <ul id="musicList">
            <li
            class="musicLi" 
            v-for="(music,index) in getMusicList" 
            :key="index"
            @click="changeindex(index)"
            :class="index==getcurrentIndex?'navgreen':'navgray'"
            >
                <div class="titlediv">
                    {{music.title}}
                </div>
            </li>
        </ul>
    </div>
</template>
<script>
    import {mapState,mapGetters,mapMutations} from 'vuex'
    export default {
        name:"list",
        computed:{
            // ...mapGetters('player',[]),
            ...mapGetters('list',["getMusicList",'getcurrentIndex'])
        },
        methods:{
            ...mapMutations('list',['SET_INDEX']),
            ...mapMutations('player',['SET_PAUSED','PAUSED']),
            swiperight(s,e){
                
            },
            swipeleft(s,e){
                this.$router.push("/",()=>{
                    console.log("player")
                })
            },
            changeindex(num){
                this.SET_INDEX(num)
                this.PAUSED()
                setTimeout(()=>{
                    console.log("start")
                    this.SET_PAUSED()
                },500)
            }
        }
    }
</script>
<style lang="scss">
    #list{
        width: 100%;
        height: 100%;
        position: absolute;
        background-color:#666;
        font-size:0.1rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        #musicList{
            height: 90%;
            display: flex;
            flex-direction: column;
            background-color:  rgba(0,0,0,0.5);
            .musicLi:first-child{
                margin-top: 0rem;
            }
            .musicLi:last-child{
                border-bottom: 1px solid #fff;
            }
            .musicLi{
                line-height: 0.4rem;
                font-size: 0.17rem;
                margin-top:0.1rem;
                list-style:none;
                height:0.4rem;
                border-top: 1px solid #fff;
                border-bottom: 1px solid #fff;
                .titlediv{
                    background-color: rgba(0,0,0,0.1);
                }
            }
        }
    }
    .navgreen{
        background-color: #ccc;
        color:black;
    }
    .navgray{
        color:#fff;
    }
</style>




